.comments__from{
    img{
        width: 50px;
        height: 50px;
        object-fit: contain;
    }
    input{
        background: transparent;
        bottom: none;
        margin-bottom: 2px solid #353946;
        color: #fff;
        &:focus{
            outline: none;
        }
    }
    button{
        background-color: #353946;
        color: #fff;
        letter-spacing: 0.5px;
        &:focus{
            bottom: none;
            outline: none;
            
        }
    }
}