@import '../../base.scss';
.videoHorizontal {
    border-bottom: 0.3px solid $border-color;
    cursor: pointer;
    &__left {
        position: relative;
        text-align: center;

    }
    &__thumbnail {
        width: 100%;

        &-wrapper {
            width: 100%;
        }
    }
    &__duration {
        position: absolute;
        bottom: 0.3rem;
        right: 0.3rem;
        padding: 0.2rem;
        background: #080808ec;
        border-radius: 3px;
    }
    &__details {
        font-size: 0.9rem;
    }
    &__title {
        font-size: 1rem;
        color: #fff;
        letter-spacing: 0.3px;
        
        @include line-clamp(1);
    }
    &__channel {
        img {
            width: 36px;
            height: 36px;
            border-radius: 50%;
            margin-right: 0.5rem;
            cursor: pointer;
        }
        p {
            font-size: 0.9rem;
            @include line-clamp(1);

        }
    }
}
@media (max-width: $breakpoint-small) {
    .videoHorizontal {
        font-size: 0.8rem;
        &__details {
                @include line-clamp(1);
        }
        &__channel {
             margin: 0.1rem 0;

             img {
                display: none;
             } 

             p {
                 font-size: 0.9rem;
             }
        }
        // &__desc {
        //     display: none;
        // }
        &__title{
            font-size: 1rem;
            @include line-clamp(2);
        }
    }
}